


































var utils = require('../../common/utils').utils;
export default {
	data () {
		return {
			list: [],
			tag: "task-status status-doing",
			status_str: "处理中"
		}
		
	},
	onLoad(e) {
		switch (e.type) {
			case 1:
				this.tag = "task-status status-doing";
				this.status_str = "处理中"

				break;
			case 2:
				this.tag = "task-status status-finish";
				this.status_str = "已处理"
				break;
		}
		this.getList();
	},
	onReachBottom() {
		this.getList();
	},
	methods: {
		getList() {
			var _this = this;
			var data = {
				type: 1
			}
			utils.requestGet("operation/getmodifylist/" + this.status_str + "/" + data, function (res) {
				console.log(res);
				if (res.code == 0) {
					_this.list = res.data;
				}
			})
		},
		goDetail(e){
			var dataset=e.currentTarget.dataset;
			uni.navigateTo({
				url:'../detail/detail?school_id='+dataset.sl+"&fault_id="+dataset.fault,
			})
		}
	}
}



